<template>
  <div class="box" v-if="essay!==null">
    <div class="bar">
      <span class="ComeBack" >
        <img src="../assets/return_image_4_0.png" @click="$router.go(-1)" alt="">
<!--        <button @click="type($route.query.category)">dianji</button>-->
        <img :src='type($route.query.category)' @click="upLocal(essay.data)" alt="">
      </span>
    </div>
    <div class="father" @click="handleHtml($event)">
      <div class="html" style="overflow-x: hidden; " v-html="essay.data.html_content"></div>
    </div>

  </div>
</template>

