    if (localStorage.getItem('fav') === null ){
      this.local={tu:[],wen:[],music:[],movie:[],radio:[]}
    }else {
      this.local = JSON.parse(localStorage.getItem('fav'))
    }
    if(this.$route.query.category === '1'){
      // http://v3.wufazhuce.com:8000/api/essay/htmlcontent/1468
      this.axios
          .get("http://v3.wufazhuce.com:8000/api/essay/htmlcontent/"+this.$route.query.id)
