    upLocal(i){
      switch (i.category) {
        case 0 : {
          let a = this.local.tu.filter(item => item.id === i.id )
          console.log(this.local.tu.filter(item => item.id !== i.id ))
          if(a.length === 0){
            this.local.tu.push(i)
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }else {
            this.local.tu = this.local.tu.filter(item => item.id !== i.id )
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
        }
        break
      }
        case 4 : {
          // let i = JSON.parse(this.$route.item)
          let a = this.local.music.filter(item => item.id === this.items.id)
          console.log(this.local.music.filter(item => item.id !==  this.items.id))
          if(a.length === 0){
            this.local.music.push( this.items)
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }else {
            this.local.music = this.local.music.filter(item => item.id !==  this.items.id)
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }
          break
        }
        case 5 : {
          let a = this.local.movie.filter(item => item.id === this.items.id )
          console.log(this.local.movie.filter(item => item.id !== this.items.id ))
          if(a.length === 0){
            this.local.movie.push(this.items)
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }else {
            this.local.movie = this.local.movie.filter(item => item.id !== this.items.id )
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }
          break
        }
        case 8 : {
          let a = this.local.radio.filter(item => item.id === this.items.id)
          console.log(this.local.radio.filter(item => item.id !== this.items.id ))
          if(a.length === 0){
            this.local.radio.push(this.items)
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }else {
            this.local.radio = this.local.radio.filter(item => item.id !==this.items.id )
            let obj = JSON.stringify(this.local)
            localStorage.setItem('fav',obj)
          }
          break
        }
        default : {
          let a = this.local.wen.filter(item => item.id === i.id )
          console.log(this.local.wen.filter(item => item.id !== i.id ))
          if(a.length === 0){
            this.local.wen.push(i)
