<template>
  <div class="home" v-if="datas !== null">
    <van-popup v-model="search" position="top" :style="{ height: '100%' }" ><search @quxiao="search = false" v-if="search" /></van-popup>
    <img class="search" src="../assets/discover_icon_search.png" @click="search = true" alt="">
    <van-tabs color="#2c3e50" class="tabs" v-model="active" sticky animated @click="list">
      <van-tab v-for="item in year"   :title="item" :key="item">
        <ul class="content" >
          <li v-for="list in datas.data" :key="list.id" @click="enterList(list.serial_id,list.title)">
            <div class="peitu">
              <span class="status">{{list.finished?'#已完结':'#连载中'}}</span>
              <img :src="list.cover" alt="">
              <h3>{{list.title}}</h3>
            </div>
            <div class="info">
              <p>{{list.forward}}</p>
              <span class="author">{{list.subtitle}}</span>
            </div>
          </li>
        </ul>
      </van-tab>
      <van-tab disabled >
      </van-tab>
    </van-tabs>

  </div>
</template>

<script>
// @ is an alias to /src
import Vue from 'vue';
import { Tab, Tabs } from 'vant';
import { Popup } from 'vant';
import search from '../components/search'
Vue.use(Popup);
Vue.use(Tab);
Vue.use(Tabs);

export default {

  name: 'Home',
  components:{
    search
  },
  created() {
    this.axios
        .get("http://v3.wufazhuce.com:8000/api/find/serial/byyear/"+this.currentTime+"?channel=yingyongbao&sign=496cb8524fd072371db30f81060e0a3a&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
        .then((res) => {
          this.datas = res.data
        });
  },
  data(){
    return{
      year:['2021','2020','2019','2018','2017','2016'],
      active:0,
      datas:null,
      search:false,
    }
  },
  computed:{
    currentTime(){
      return this.year[0]
    }
  },
  methods:{
    list(name,title){
      this.axios
          .get("http://v3.wufazhuce.com:8000/api/find/serial/byyear/"+title+"?channel=yingyongbao&sign=496cb8524fd072371db30f81060e0a3a&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
          .then((res) => {
            this.datas = res.data
          });
    },
    enterList(serial_id,titles){
      this.$router.push({
        path:'/seriallist',
        query:{
          serial_id,
          titles
        }
      })
    }
  }
}
</script>

