<template>
  <div class="page" >
    <Author :ren="ren" @quxiao="quxiao" v-if="isAuthor"/>
    <TuWen :tu="tu" v-if="isTuwen" />
    <form class='sousuo' action="/" >
      <van-sticky>
        <van-search
            class="ssinput"
            autofocus
            v-model="value"
            show-action
            placeholder="在这里写下你想寻找的"
            @search="onSearch"
            @cancel="onCancel"
        />
      </van-sticky>
    </form>
    <div class="show" v-if="isShow">
      <div class="box" v-if="$route.path === '/find'" >
        <ul v-if="ac === 0 " >
          <li @click="mode(index+1)" v-for="(item,index) in sort" :key="index">{{item}}</li>
        </ul>
        <ul class="list">
          <li v-for="item in list" :key="item.id"></li>
        </ul>
      </div>
    </div>
    <div class="search" v-else>
      <van-tabs color="#2c3e50" @change="change" v-model="active" sticky offset-top="54px" v-if="result!==null" >
        <van-tab   v-for="item in sorts"  :title="item" :key="item"  >
<!--         v-if="result!==null" -->

          <ul v-if="active===0">
            <li v-for="i in result.data.list" :key="i.content_id" >
              <div class="boxs">
                <img class="imgs"  :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
          <ul v-if="active===1">
            <li v-for="i in result.data.list" :key="i.content_id" @click="enter(i.content_id,i.category,i)">
              <div class="boxs">
                <img class="imgs" :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
          <ul v-if="active===2">
            <li v-for="i in result.data.list" :key="i.music_id" @click="enter(i.music_id,i.category,i)">
              <div class="boxs">
                <img class="imgs" :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
<!--这路运行会报错-->
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
          <ul v-if="active===3">
            <li v-for="i in result.data.list" :key="i.content_id" @click="enter(i.content_id,i.category,i)">
              <div class="boxs">
                <img class="imgs" :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
          <ul v-if="active===4">
            <li v-for="i in result.data.list" :key="i.content_id" @click="enter(i.content_id,i.category,i)">
              <div class="boxs">
                <img class="imgs" :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
          <ul v-if="active===5">
            <li v-for="i in result.data.list" :key="i.content_id" @click="zuozhe(i.content_id)">
              <div class="boxs">
                <img class="imgs" style="border-radius: 50px" :src="i.cover" alt="">
                <div class="info">
                  <p>{{ i.title }}</p>
                  <span>{{i.subtitle}}</span>
                </div>
              </div>
              <hr>
            </li>
            <li class="no" v-if="(result.data?(result.data.list?result.data.list.length:1):1) === 0"><img class="null" src="../assets/no_search_result.png" alt=""></li>
          </ul>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import TuWen from './TuWen'
import Author from './Author'
import { Search } from 'vant';
import { Tab, Tabs } from 'vant';
import { Sticky } from 'vant';

Vue.use(Sticky);
Vue.use(Tab);
Vue.use(Tabs);
// import { Toast } from 'vant';

Vue.use(Search);
export default {
  name: "search",
  components:{
    TuWen,Author
  },
  data(){
    return{
      value: '',
      sort:['图文','问答','阅读','影视','音乐','电台'],
      ac:0,
      yingshimin:new Date(2016, 0),
      minDate: new Date(2012, 9),
      maxDate: new Date(2025, 10),
      currentDate: new Date(),
      list:null,
      isShow: true,
      active: 0,
      sorts:['图文','阅读','音乐','影视','ONE电台','作者/音乐人'],
      val:null,
      result:null,
      tu:null,
      isTuwen:false,
      ren:null,
      isAuthor:false
    }
  },
  methods: {
    quxiao(){
      this.isTuwen=false
    },
    zuozhe(id){
      this.ren = id
      this.isAuthor = true
    },
    tuwen(a){
      console.log(a)
      this.tu = a
      this.isTuwen = false
    },
    enter(id,category,item){
      this.$router.push({
        path:'/detail',
        query:{
          id,category,item:JSON.stringify(item)
        }
      })
    },
    onSearch(val) {
      this.val = val

      this.isShow = false
      if (this.active ===0){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/hp/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(this.active === 1){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/reading/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      } else if(this.active === 2){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/music/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(this.active === 3){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/movie/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(this.active === 4){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/radio/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(this.active === 5){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/author/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }
    },
    onCancel() {
      // Toast('取消');
      this.$emit('quxiao')
    },
    mode(index){
      this.ac = index
      console.log(index)
      this.$emit('mode',index)

    },
    change(name,title){
      title
      if (name ===0){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/hp/"+ this.val +"/")
            .then((res) => {
              this.result = res.data
            });
      }else if(name === 1){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/reading/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      } else if(name === 2){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/music/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(name === 3){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/movie/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(name === 4){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/radio/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }else if(name === 5){
        this.axios
            .get("http://v3.wufazhuce.com:8000/api/search/author/"+ this.val +"/0")
            .then((res) => {
              this.result = res.data
            });
      }
  },
}
}
</script>

<style lang="less" scoped>

  .page{
    //z-index: 999;
    width: 100vw;
    height: 100vh;
    //background-color:white;
    opacity: 0.8;
    //position: fixed;
    display: flex;
    flex-direction: column;
    .sousuo{
      //height:100vh;
      .ssinput{
        position: sticky;

      }
      //position: sticky;
      top: 0;
      width: 100vw;
      //background-color: #8e8e8e;
      .van-field__control{
        //background-color: white !important;
      }
    }
    .no{
      display: flex;
      justify-content: center;
      //align-items: center;
      .null{
        width: 40vw;
        margin-top: 50px;
        //margin: 0 auto;
      }
    }
    .show{
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .box{
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        ul{
          li{
            color: #6e6e6e;
            min-height: 50px;

          }
        }
      }
    }
    .search{
      flex: 1;
      //overflow: hidden;
      ul{
        margin-top: 10px;
        margin-bottom: 50px;
        //height: 80vh;
        li{
          &:last-of-type hr{
            margin-bottom: -10px;
          }
          hr{
            transform: scale(1,0.5);
          }
          //box-shadow: 0 0 0 0.5px #8e8e8e;
          .boxs{
            display: flex;
            justify-content: space-between;
            .imgs{
              width: 50px;
              height: 50px;
              background-size: cover;
              margin-left: 5vw;
            }
            .info{
              display:flex;
              flex: 1;
              flex-direction: column;
              justify-content: space-between;
              //padding: 0 5vw;
              padding-left: 15px;
              p{
                margin: 0;
                //width: 70vw;
                width: 75vw;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
              }
              span{
                margin-top: 10px;
                color: #8e8e8e;
                font-size: 13px;
              }
            }
          }
        }
      }
    }

  }
</style>