<script>
export default {
  name: "TuWen",
  props:{
    tu:Object
  },
  data(){
    return{
      local:null
    }
  },
  created() {
    if (localStorage.getItem('fav') === null ){
      this.local={tu:[],wen:[],music:[],movie:[],radio:[]}
    }else {
      this.local = JSON.parse(localStorage.getItem('fav'))
    }
  },
  methods:{
    upLocal(tu){

      let a = this.local.tu.filter(item =>  ( item.volume ? item.volume : item.subtitle) === ( tu.volume ? tu.volume : tu.subtitle))
      console.log(a )
      // console.log(this.local.tu.filter(item =>  ( item.volume ? item.volume : item.subtitle)!== ( tu.volume ? tu.volume : tu.subtitle)))
      if(a.length === 0){
        this.local.tu.push(this.tu)
        let obj = JSON.stringify(this.local)
        localStorage.setItem('fav',obj)
      }else {
        this.local.tu = this.local.tu.filter(item => ( item.volume ? item.volume : item.subtitle)!== ( tu.volume ? tu.volume : tu.subtitle))
        let obj = JSON.stringify(this.local)
        localStorage.setItem('fav',obj)
      }

      // if()
      // let newboj =


      // console.log(item)
      // this.s.a.push(item)
      // var obj = JSON.stringify(this.s)
      // localStorage.setItem('fav',obj)
      // var sss = JSON.parse(localStorage.getItem('fav'))
      // console.log(sss)
    },
  }
}
</script>

