          box-shadow: 0 10px 20px -15px #999;

          display: flex;
          flex-direction: column;
          justify-content: space-between;
          span{
            text-align: center;
            margin-top: 10px;
            color: #8e8e8e;
            font-size: 12px;
          }
          p{
            padding: 20px;
            line-height: 20px;
            text-indent:2rem;
            font-size: 13px;
            text-align: left;

          }
          .info{
            margin-bottom: 30px;
          }
        }
      }
      .bar{
        margin-top: 20px;
        width: 100%;
        height: 50px;
        ul{
          margin-left: 9vw;
          display: flex;
          li{
            display: flex;
            margin-left: 15vw;
            align-items: center;
            font-size: 12px;
            &:first-child {
              margin-left: 0;
            }
            &:nth-child(2){
              margin-left: 30vw;
            }
            &:nth-child(3){
              margin-left: 13vw;
            }
            &:last-child{
              margin-left: 4vw;
            }

            img{
              width: 25px;
              background-size: cover;
            }
            &:nth-child(2) img{
              width: 18px;

            }
          }
        }
      }

    }
