<template>
  <div class="box" v-if="list!==null">
    <TuWen :tu="tu" v-if="isTuwen" @quxiao="quxiao"></TuWen>
    <div class="bar">
      <span class="ComeBack" ><img src="../assets/return_image_4_0.png" @click="$router.go(-1)" alt=""></span>
      <span class="title">小记广场</span>
    </div>
    <van-list
        class="l"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
    >
      <div class="van-clearfix">
      <div class="list " v-for="item in list.data.list" :key="item" :title="item.id" @click="tuwen(item)">
<!--        <div class="list">-->
          <img class="img" :src="item.img_url" alt="">
          <div class="info">
            <p class="words">{{item.content}}</p>
            <span class="user" >{{ item.user.user_name }}</span>
            <span class="time">{{item.input_date.split(' ')[0]}} · {{item.addr}}</span>
          </div>
<!--        </div>-->
      </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import Vue from 'vue';
import { List } from 'vant';
import TuWen from '../components/TuWen'

Vue.use(List);
export default {
  name: "Search",
  components: {TuWen},
  created() {
    this.axios
        .get("http://v3.wufazhuce.com:8000/api/diary/square/more/0?channel=yingyongbao&sign=496cb8524fd072371db30f81060e0a3a&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
        .then((res) => {
          this.list = res.data
        });
  },
  data(){
    return {
      isTuwen:false,
      tu:null,
      list: null,
      loading: false,
      finished: false,
      add:null
    };
  },
  watch:{
    add(){
      console.log(123)
      this.list.data.list.push(this.add[0])
    }
  },
  methods:{
    quxiao(){
      this.isTuwen =false
    },
    tuwen(item){
      this.tu = item
      this.isTuwen = true
    },
    onLoad() {
      // 异步更新数据
      let timeout = -1  //写在外边


        this.sup = !this.sup
        clearTimeout(timeout)
        timeout = setTimeout(() => {
          console.log(this.list.data.list[this.list.data.list.length-1].id)
          // setTimeout 仅做示例，真实场景中一般为 ajax 请求
          this.axios
              .get("http://v3.wufazhuce.com:8000/api/diary/square/more/"+ this.list.data.list[this.list.data.list.length-1].id +"?channel=yingyongbao&sign=496cb8524fd072371db30f81060e0a3a&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
              .then((res) => {
                console.log(res.data.data.list)
                this.add = res.data.data.list
              });


          // 加载状态结束
          this.loading = false;
          clearTimeout(timeout)
        }, 1000)
      }


        // 数据全部加载完成

    },
    onCancel() {
      // Toast('取消');
      this.$router.go(-1)
    },

}
</script>

