    top: 0;
    margin: 0;
    padding: 0;

    .ComeBack{
      z-index: 1;
      padding: 0 5px;
      display: flex;
      justify-content: space-between;
      img{
        width: 20px;
        //background-size: cover;

        margin-top: 10px;
        height: 20px;
      }

    }
    .title{
      flex: 1;
      text-align: center;
      //item-self: center;
      transform:translateX(-20px);
      //margin: 0;
      margin-top: 10px;
      //margin-right: 20px;
      //width: 20px;
    }

  }
  .l{
    //margin-top: 40px;

  }
  .l /deep/ .van-list{
    //margin-top: 40px;
  }
  .van-clearfix{
    //-webkit-column-gap: 0;
    -webkit-column-width: 42.5vw;
    padding: 0 20px 50px 20px;
    //padding-bottom: 50px;
    //column-count: 2;
    //float: left;
    //display: flex;
    //flex-wrap: wrap;
  .list{
    //margin-left: 20px;
    //padding: 20px;
    break-inside: avoid;
    float: left;
    margin-bottom: 20px;
    background-color: white;
    height: auto;
    width: 42.5vw;
    //padding: 15px 0 0 15px;
    //margin: 0 5vw;
    .img{
      width: 42.5vw;
      height: auto;

      //background-size: cover;
    }
    .info{
      padding: 5px;
      display: flex;
      flex-direction: column;
      .words{
        //text-overflow: -o-ellipsis-lastline;
        margin: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      .user{
        color: #8e8e8e;
        font-size: 14px;
      }
      .time{
        color: #8e8e8e;
        font-size: 14px;
      }
    }
  }
}
}
