<template>
  <div class="boxs" v-if="author !== null">
    <div class="nav">
      <img class="quxiao" src="../assets/return_image_4_0.png" @click="$parent.isAuthor = false" alt="">
    </div>
    <div class="info">
      <img :src="author.data[0].author.web_url" alt="">
      <h3>{{ author.data[0].author.user_name }}</h3>
      <span>{{ author.data[0].author.summary}}</span>
      <h5>{{author.data[0].author.desc}}</h5>
      <span class="fans">{{author.data[0].author.fans_total}}关注</span>
    </div>
    <div class="content">
      <div class="item" v-for="item in author.data" :key="item.id">
        <div class="type1" v-if="item.category !== '8'&& item.category !== '6'" @click="enter(item.content_id,item.category,item)">
          <span>- {{reType(item)}} -</span>
          <h3>{{item.title}}</h3>
          <span class="words_info">{{item.content_type==="4"?'':'文 /'}} {{item.author.user_name}}</span>
          <p>{{item.forward}}</p>
          <div class="content_img">
            <img class="peitu"  :src="item.img_url" alt="">
          </div>
          <div class="bar">
            <ul>
              <li><img src="../assets/discover_laud_unselected.png" alt="">&nbsp;&nbsp;&nbsp;{{item.like_count}}</li>
              <li><img src="../assets/discover_repost.png" alt=""></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Author",
  props:{
    ren:Number
  },
  created() {
    this.axios
      .get('http://v3.wufazhuce.com:8000/api/author/works?page_num=0&author_id=' + this.ren)
      .then((res)=>{
        this.author = res.data
          })
  },
  data(){
    return{
      author:null
    }
  },
  methods:{
    reType(x){
      if( x.tag_list.length === 0){
        switch (x.content_type){
          case "1" :  return '阅读';
          case "2" :  return '连载';
          case "3" :  return '问答';
          case "4" :  return '音乐';
          case "5" :  return '影视';
          case "6" :  return '广告';
          case "7" :  return 'ONE STORY';
          case "8" :  return '电台';
          default : break
        }
      }else {
        return x.tag_list[0].title
      }
    },
    enter(id,category,item){
      let obj = JSON.stringify(item)
      localStorage.setItem('item',obj)
      this.$router.push({
        path:'/detail',
        query:{
          id,category,
        }
      })
    },
    enters(){
      this.$router.push({
        path:'/search',

      })
    }
  }
}
</script>

<style lang="less" scoped>
.boxs{
  background-color: #f7f7f7;
  position: fixed;
  overflow: scroll;
  top: 0;
  z-index:999999;
  width: 100vw;
  height: 100vh;
  .nav{
    position: relative;
    .title{
      position: absolute;
      top: 10px;
      left: 46%;
      font-weight: bold;
      font-size: 18px;
      //margin: 0 auto;
    }
    .quxiao{
      z-index: 999;
      position: fixed;
      top: 10px;
      left: 5px;
      width: 20px;
      height: 20px;
    }
  }
  .info{
    margin-bottom: 5px;
    background-color: white;
    padding: 25px 0;

    display: flex;
    flex-direction: column;
    //justify-content: center;
    align-items: center;
    img{
      width: 70px;
      border-radius: 50%;
    }
    span{
      color: #8e8e8e;
      font-size: 12px;
    }
    h5{
      width: 85vw;
      text-align: center;
    }
  }
  .content{
    margin-bottom: 5px;
    .item {
      margin-bottom: 5px;
      &:last-of-type {
        padding-bottom: 30px;
      }
      .type1{


        padding: 5vw;

        padding-bottom: 0;
        display: flex;
        flex-direction: column;
        //justify-content: center;
        background-color: white;


        span {
          text-align: center;
          margin-top: 10px;
          font-size: 12px;
          color: #8e8e8e;
        }

        h3 {
          margin-bottom: 0;
          text-align: left;
        }

        p {
          text-align: left;
          font-size: 14px;
        }

        .words_info {
          text-align: left;
          margin-bottom: 10px;
        }

        .content_img {
          .peitu {
            border-radius: 10px;
            width: 90vw;
            margin: 0 auto;
            background-size: cover;
          }

          .info {
            width: 80%;
          }
        }

        .bar {
          margin-top: 20px;
          width: 100%;
          height: 50px;

          ul {
            display: flex;
            justify-content: flex-end;

            li {
              display: flex;
              font-size: 12px;
              margin-right: 2vw;
              align-items: center;

              &:first-child {
                margin-right: 7vw;
              }

              img {
                width: 25px;
                background-size: cover;
              }
            }
          }
        }
      }
    }
  }
}
</style>