<template>
  <div class="list" v-if="list!==null">
    <div class="bar">
      <span class="ComeBack" @click="$router.go(-1)"><img src="../assets/return_image_4_0.png" alt=""></span><h3>{{$route.query.titles}}</h3>
    </div>
    <ul>
      <li v-for="(item,index) in list.data" :key="item.id" @click="enter(item.id,item.category,item)">
        <span>No.{{index+1}}</span>
        <img :src="item.cover" alt="">
        <div class="info">
          <h3>{{item.title}}</h3>
          <p>{{item.subtitle}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: "SerialList",
  created() {
    this.axios
        .get("http://v3.wufazhuce.com:8000/api/find/rank/"+ this.$route.query.id +"?channel=yingyongbao&sign=496cb8524fd072371db30f81060e0a3a&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
        .then((res) => {
          this.list = res.data
          console.log(res)
        });
  },
  data(){
    return{
      list:null
    }
  },
  methods:{
    enter(id,category,item){
      let obj = JSON.stringify(item)
      localStorage.setItem('item',obj)
      this.$router.push({
        path:'/detail',
        query:{
          id,category,
        }
      })
    }
  }
}

</script>

<style lang="less" scoped>
.list{
  .bar{
    z-index: 2;

    top: 0;
    position: fixed;
    background-color: white;
    padding: 8px 0;
    width: 100vw;
    height: 30px;
    .ComeBack{
      padding: 0 5px;
      img{
        width: 25px;
      }
    }
    h3{
      margin: 0;
      width: 90vw;
      text-align: left;
      display: inline-block;
      white-space:nowrap;
      overflow:hidden;
      text-overflow:ellipsis;
    }
  }
  ul{
    margin: 30px auto 50px auto;
    width: 90vw;

    display: flex;
    flex-direction: column;
    li{
      display: flex;
      justify-content: space-around;
      position: relative;
      margin-top: 20px;
      &:first-child  span{
        background-color: #d80505;
      }
      &:nth-child(2)  span{
        background-color: #ff5555;
      }
      span{
        left: 0;
        color: white;
        border-radius: 6px 0;
        font-size: 12px;
        line-height: 12px;
        padding: 2px 5px ;
        //z-index: 9;
        background-color: #ffa100;
        position: absolute;
      }
      img{
        width: 15vw;
        height: 15vw;
        border-radius: 10px;
      }
      .info {
        width: 75vw;
        h3 {
          margin: 5px 20px;
          text-align: left;

          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        p {
          text-align: left;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          color: #8e8e8e;
          font-size: 14px;

          margin: 10px 0 0 20px;
        }
      }
    }
  }
}
</style>