<template>
 <div class="list" v-if="list !== null">
   <div class="bar">
     <span class="ComeBack" @click="$router.go(-1)"><img src="../assets/return_image_4_0.png" alt=""></span><h3>{{$route.query.titles}}</h3>
   </div>
   <ul>
     <li v-for="item in list.data" :key="item.id" @click="enter(item.id,item.category,item)">
       <img :src="item.cover" alt="">
       <div class="info">
         <h3>{{item.title}}</h3>
         <p>{{item.subtitle}}</p>
       </div>
     </li>
   </ul>
 </div>
</template>

<script>
export default {
  name: "SerialList",
  created() {
    this.axios
        .get("http://v3.wufazhuce.com:8000/api/find/serial/list/"+ this.$route.query.serial_id +"?user_id=12108744&channel=yingyongbao&sign=8b637ca6837629688f51a50ce021ec42&version=5.1.9&uuid=1d695bfc774ca36d3a51a9208b5141007&platform=android")
        .then((res) => {
          this.list = res.data
          console.log(res)
        });
  },
  data(){
    return{
        list:null
      }
    },
methods:{
  enter(id,category,item){
    let obj = JSON.stringify(item)
    localStorage.setItem('item',obj)
    this.$router.push({
      path:'/detail',
      query:{
        id,category,
      }
    })
  },
}
}

</script>

<style lang="less" scoped>
  .list{
    .bar{
      top: 0;
      position: fixed;
      //margin: 5px;
      padding: 10px 0;
      background-color: white;
      width: 100vw;
      height: 30px;
      .ComeBack{
        padding: 0 5px;
        img{
          width: 25px;
        }
      }
      h3{
        margin: 0;
        width: 90vw;
        text-align: left;
        display: inline-block;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
      }
    }
    ul{

      margin: 30px auto 20px auto;
      width: 90vw;

      display: flex;
      flex-direction: column;
      li{
        display: flex;
        justify-content: space-around;

        margin-top: 20px;
        img{
          width: 15vw;
          height: 15vw;
          border-radius: 10px;
        }
        .info {
          width: 75vw;
          h3 {
            margin: 5px 20px;
            text-align: left;

            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          p {
            text-align: left;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: #8e8e8e;
            font-size: 14px;

            margin: 12px 0 0 20px;
          }
        }
      }
    }
  }
</style>