<script>
// @ is an alias to /src
import Vue from 'vue';
import TuWen from '../components/TuWen'
import { Popup } from 'vant';
import { Grid, GridItem } from 'vant';
import { Image as VanImage } from 'vant';

Vue.use(VanImage);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Popup);

export default {
  name: 'Mine',
  components: {

    TuWen
  },
  created() {
    if (localStorage.getItem('fav') === null ){
      this.local={tu:[],wen:[],music:[],movie:[],radio:[]}
    }else {
      this.local = JSON.parse(localStorage.getItem('fav'))
    }
  },
  data() {
    return {
      isTuWen:false,
      show: false,
      list:null,
      local:null,
      type:null,
      tu:null
    };
  },
  watch:{
    type(){
      switch (this.type){
        case 1:{
          this.list = this.local.tu
          break
        }
        case 2:{
          this.list = this.local.wen
          break
        }
        case 3:{
          this.list = this.local.music
          break
        }
        case 4:{
          this.list = this.local.movie
          break
        }
        case 5:{
          this.list = this.local.radio
          break
        }
      }
    }
  },
  methods: {
    enter(id,category,item){
      this.$router.push({
        path:'/detail',
        query:{
          id,category,item:JSON.stringify(item)
        }
      })
    },
    quxiao(){
      console.log(123)
      this.isTuWen = false
    },
    showPopup() {
      this.show = true;
    },
    tuwen(item){
      console.log(123)
      this.tu = item
      this.isTuWen = true
    },
  },
}
</script>
