<script>
import Vue from 'vue';
import { PullRefresh } from 'vant';
import {mapMutations} from 'vuex'
// import { Sticky } from 'vant';

// Vue.use(Sticky);
Vue.use(PullRefresh);
import { Image as VanImage } from 'vant';


Vue.use(VanImage);

export default {
  name: 'Home',
  data(){
    return{
      flag:false,
      data:null,
      isLoading: false,
      date:null,
      years:null,
      month:null,
      day:null,
      local:{tu:[],wen:[],music:[],movie:[],radio:[]}
    }
  },
  components: {


  },
  created() {

    if (localStorage.getItem('fav') === null ){
      this.local={tu:[],wen:[],music:[],movie:[],radio:[]}
    }else {
      this.local = JSON.parse(localStorage.getItem('fav'))
    }
    this.axios
        // .get('http://v3.wufazhuce.com:8000/api/channel/one/2021-11-21/0')
        .get("http://v3.wufazhuce.com:8000/api/channel/one/0/0")
        .then((res) => {
          this.data = res.data
          this.index(res.data)
        });
    this.date = new Date()


    },
  watch:{
    date(){
      this.years = this.date.getFullYear()
      this.day = this.date.getDate()
      switch (this.date.getMonth() +1 ){
        case 1  : return this.month = 'Jan.'
        case 2  : return this.month = 'Feb.'
        case 3  : return this.month = 'Mar.'
        case 4  : return this.month = 'Apr.'
        case 5  : return this.month = 'May.'
        case 6  : return this.month = 'Jun.'
        case 7  : return this.month = 'Jul.'
        case 8  : return this.month = 'Aug.'
        case 9  : return this.month = 'Sep.'
        case 10 : return this.month = 'Oct.'
        case 11 : return this.month = 'Nov.'
        case 12 : return this.month = 'Dec.'
        default: console.log(this.date.getMonth() +1 )
      }


    }
  },
  methods:{
    ...mapMutations(['index']),
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
      }, 500);
    },
    upLocal(i){
      console.log(1111)
      console.log(this.local.tu.filter(item =>  ( item.volume ? item.volume : item.subtitle ) !== i.volume))
      let a = this.local.tu.filter(item =>( item.volume ? item.volume : item.subtitle) === i.volume)
      console.log(this.local.tu.filter(item =>  ( item.volume ? item.volume : item.subtitle)!== i.volume).length)
      if(a.length === 0){
        this.local.tu.push(i)
        let obj = JSON.stringify(this.local)
        localStorage.setItem('fav',obj)
      }else{
        console.log(123,'删除')
        this.local.tu = this.local.tu.filter(item => ( item.volume ? item.volume : item.subtitle ) !== i.volume)
        let obj = JSON.stringify(this.local)
        localStorage.setItem('fav',obj)
      }
    },

    reType(x){
      if( x.tag_list.length === 0){
        switch (x.content_type){
          case "1" :  return '阅读';
          case "2" :  return '连载';
          case "3" :  return '问答';
          case "4" :  return '音乐';
          case "5" :  return '影视';
          case "6" :  return '广告';
          case "7" :  return 'ONE STORY';
          case "8" :  return '电台';
          default : break
        }
      }else {
        return x.tag_list[0].title
      }
    },
    enter(id,category,item){
      let obj = JSON.stringify(item)
      localStorage.setItem('item',obj)
      this.$router.push({
        path:'/detail',
        query:{
          id,category,
        }
      })
    },
    enters(){
      this.$router.push({
        path:'/search',

      })
    }
  }
}
</script>

